<template>
  <div id="synopsis">
    <i class="el-icon-back allback" @click="$emit('back')"></i>
    <i @click="openTextFn" v-if="isLogin" class="el-icon-edit pen cursorp" title="修改内容"></i>
    <div class="blogx" style="padding-bottom:20px;" v-html="content"></div>

    <bianjiq
      v-if="bianjiFlage"
      class="bianjiqi"
      :quxiao="ruxiao"
      :queding="queding"
      :startext="content"
    />
  </div>
</template>

<script>
import { mapState,mapMutations } from "vuex";
// 引入编辑器
import bianjiq from "../../components/wangeditor";
export default {
  components: {
    bianjiq //编辑器
  },
  computed: {
    ...mapState([
      "isLogin", // 是否登录
      "isPC",
      "allnavs",
      "USER_ID" // 用户ID
    ])
  },
  data() {
    return {
      bianjiFlage: false,
      content: `<h1 style="text-align: center;">个人简历详情... ...</h1><p><br></p>`
    };
  },
  methods: {
    ...mapMutations([
      "setLoadingFlage", //加载框
    ]),
    openTextFn() {
      //  判断
      if (!this.isPC)
        return this.$notify.error({
          title: "请在PC端编辑内容哟",
          customClass: "alter"
        });
      this.bianjiFlage = true;
    },
    ruxiao() {
      this.bianjiFlage = false;
    },
    async queding(content) {
      this.setLoadingFlage(true)
      // 修改数据
      let { data: res } = await this.$http.post("/resume", { content });
      this.setLoadingFlage(false)
      if (res.ok == 0)
        return this.$notify.error({
          title: "修改失败！",
          customClass: "alter"
        });
      // 获取数据
      this.getContent();
      this.$notify.success({
        title: "修改成功~",
        customClass: "alter"
      });
      this.bianjiFlage =false;
    },
    // 获取简介数据
    async getContent() {
      let { data: res } = await this.$http.get("/resume/" + this.USER_ID);
      this.content = res.content;
    }
  },
  created() {
    // 判断是否有ID
    if(this.USER_ID) this.getContent()
    if (!this.isPC) {
      this.bianjiFlage = false;
    }
  },
  watch: {
    allnavs: {
      handler(allnavs) {
        // 判断是否允许访问
        if (allnavs.navs && allnavs.navs.split(",")[0] != 1) {
          return this.$router.push("/");
        }
      },
      // 深度监听
      deep: true
    },
    USER_ID: {
      handler(userID) {
        // 获取数据
        if (userID != "") this.getContent();
      },
      immediate: true
    }
  }
};
</script>

<style>
.pen {
  font-size: 40px;
  border: 2px solid #b4b4b4;
  padding: 5px;
  border-radius: 50%;
  position: fixed;
 bottom: 10%;
  float: right;
  color: #949495;
  right: 0px;
}
.pen:hover {
  color: #b4b4b4;
}
</style>